    background: #7b4e5d;
    -webkit-box-shadow: 0px 6px 69px -16px rgba(0,0,0,0.75);
    box-shadow: 0px 6px 69px -16px rgba(0,0,0,0.75);
    }
.textarea{
    width: 45%;
    display: block;
    height: 100%;
}


